.profile {
  width: 100%;

  .profileSection {
    max-width: 900px;
    width: auto;
    margin: 100px auto 60px auto;
    display: flex;
    border-bottom: 1px solid rgba(var(--ca6, 219, 219, 219), 1);
    padding-bottom: 60px;

    .avatar {
      height: 150px;
      width: 150px;
      border-radius: 50%;
      margin: 0px 150px 0px 0px;
    }
    .profileStats {
      .firstRow {
        margin: 40px 0px 20px 0px;
        display: flex;
        .userName {
          margin: 0px 25px 0px 0px;
          font-size: 28px;
          font-weight: 300;
        }
        a {
          color: #111111;
          text-decoration: none;
        }
        .editBtn {
          padding: 5px 10px 5px 10px;
          border: 1px solid rgba(var(--ca6, 219, 219, 219), 1);
          cursor: pointer;
          color: #111111;
          text-decoration: none;
        }
        .followBtn {
          padding: 5px 10px;
          margin: 0px 0px 0px 25px;
          border: 1px solid rgba(var(--ca6, 219, 219, 219), 1);
          cursor: pointer;
        }
      }
      .secondRow {
        .followers {
          margin: 0px 60px;
        }
      }
    }
  }
  .postSection {
    max-width: 900px;
    margin: 0px auto;
    .noPosts {
      text-align: center;
    }
    .post {
      width: 30%;
      max-height: 300px;
      height: 300px;
      object-fit: scale-down;
    }
    .post:not(:nth-child(3n)) {
      margin-right: 10px;
    }
  }
}
