body {
  box-sizing: border-box;
  margin: 0px;
  /* padding-bottom: 20%; */
  background-color: #fafafa;
  font-size: 16px;
  scroll-behavior: smooth;
}

.container {
  /* display: flex; */
  align-items: center;
  justify-content: center;
  /* margin-bottom: 30px;
  margin-top: 140px; */
  margin: 130px auto 10px auto;
  width: 100%;
  border: 1px solid #e6e6e6;
  max-width: 300px;
}
.container p {
  color: grey;
  font-size: 20px;
  text-align: center;
}
.branding {
  font-family: 'Pacifico', cursive;
  text-align: center;
}
.inputContainer {
  border-bottom: 0;
  width: 80%;
  margin: 5px auto 0px auto;
  position: relative;
}
.inputContainer span {
  position: absolute;
  left: 5px;
  font-size: 12px;
  color: grey;
  text-transform: capitalize;
}
.inputContainer input {
  border: 0.5px solid #efefef;
  color: #fafafa;
  width: 100%;
  height: 100%;
  border-bottom: 0.5px solid #efefef;
  padding: 15px 0px 5px 5px;
  color: black;
  font-size: 12px;
  z-index: 12037123123;
}
.inputContainer input:focus {
  outline-color: grey;
}
.redirection {
  align-items: center;
  justify-content: center;
  margin: 20px auto 10px auto;
  width: 100%;
  border: 1px solid #e6e6e6;
  max-width: 300px;
  height: 50px;
  margin: auto;
  text-align: center;
  padding-top: 30px;
}
.redirection a {
  text-decoration: none;
  color: #369cee;
}
.container form button {
  border: 0;
  align-items: center;
  margin: 20px 0px 10px 30px;
  width: 100%;
  border-radius: 5px;
  max-width: 245px;
  padding: 5px 0;
  background-color: #369cee;
  color: #ffffff;
  cursor: pointer;
}
.error {
  color: red;
  font-size: 12px;
  margin-left: 8px;
  padding: 0;
  margin: 0px 0px 0px 30px;
}
